:root{--c-purple-primary: #6809E1;--c-purple-secondary: #572EE5;--c-bg: #FFFFFF;--c-bg-gifts: #E7E7E7;--c-black: #292922;--c-placeholder: #3F3F3F;--c-bg-li: #EEEEEE;--c-border-li: #DDDDDD}[data-theme="dark"]{--c-bg: #373737;--c-bg-gifts: #212121;--c-purple-secondary: #FFFFFF;--c-placeholder: #FFFFFF;--c-bg-li: #998CEB;--c-border-li: #DDDDDD}*{margin:0;padding:0;box-sizing:border-box;list-style:none}html,button{font-family:'Roboto', sans-serif}.App{min-height:100vh;background-color:#FFFFFF;background-color:var(--c-bg);text-align:center;color:#572EE5;color:var(--c-purple-secondary);transition:all 0.8s ease}button{cursor:pointer}input,button{outline:none;background:none}

.bar{height:6px;width:100%;background-color:var(--c-purple-primary)}.header-container{max-width:750px;height:60px;margin:26px auto;padding:0 26px;display:flex;justify-content:space-between;align-items:center}.btn{height:43px;width:148px;border-radius:60px;border:1px solid var(--c-purple-secondary);font-weight:bold;color:var(--c-purple-secondary)}

:root{--c-purple-primary: #6809E1;--c-purple-secondary: #572EE5;--c-bg: #FFFFFF;--c-bg-gifts: #E7E7E7;--c-black: #292922;--c-placeholder: #3F3F3F;--c-bg-li: #EEEEEE;--c-border-li: #DDDDDD}[data-theme="dark"]{--c-bg: #373737;--c-bg-gifts: #212121;--c-purple-secondary: #FFFFFF;--c-placeholder: #FFFFFF;--c-bg-li: #998CEB;--c-border-li: #DDDDDD}*{margin:0;padding:0;box-sizing:border-box;list-style:none}html,button{font-family:'Roboto', sans-serif}.App{min-height:100vh;background-color:#FFFFFF;background-color:var(--c-bg);text-align:center;color:#572EE5;color:var(--c-purple-secondary);transition:all 0.8s ease}button{cursor:pointer}input,button{outline:none;background:none}.section-search,.input-container,img{margin:auto}.section-search{max-width:540px;padding:40px 20px}h1{padding-bottom:15px;font-weight:400}span{font-weight:bold}img{display:block}@media (max-width: 768px){.img-header{width:300px;height:100%}}.form{position:relative;margin:auto;width:90%}.input-container{display:flex;width:100%;height:50px}input,.btn-search{height:100%;border:1px solid #572EE5;border:1px solid var(--c-purple-secondary)}input{width:85%;padding:20px;border-radius:60px 0 0 60px;font-size:18px;color:#572EE5;color:var(--c-purple-secondary)}input::-webkit-input-placeholder{color:#3F3F3F;color:var(--c-placeholder)}input:-ms-input-placeholder{color:#3F3F3F;color:var(--c-placeholder)}input::placeholder{color:#3F3F3F;color:var(--c-placeholder)}.btn-search{width:15%;border-radius:0 60px 60px 0;background-color:#572EE5;background-color:var(--c-purple-secondary)}.suggestion-list{width:100%;top:60px;position:absolute;background-color:#FFFFFF;background-color:var(--c-bg);border:1px solid #572EE5;border:1px solid var(--c-purple-secondary);border-radius:30px;overflow:hidden}li{display:flex;align-items:center;height:45px;padding:0 15px;text-align:left;cursor:pointer;border-bottom:1px solid #DDDDDD;border-bottom:1px solid var(--c-border-li)}li:hover{background-color:#EEEEEE;background-color:var(--c-bg-li)}.search-icon{display:inline;margin:0 10px;width:20px}

:root{--c-purple-primary: #6809E1;--c-purple-secondary: #572EE5;--c-bg: #FFFFFF;--c-bg-gifts: #E7E7E7;--c-black: #292922;--c-placeholder: #3F3F3F;--c-bg-li: #EEEEEE;--c-border-li: #DDDDDD}[data-theme="dark"]{--c-bg: #373737;--c-bg-gifts: #212121;--c-purple-secondary: #FFFFFF;--c-placeholder: #FFFFFF;--c-bg-li: #998CEB;--c-border-li: #DDDDDD}*{margin:0;padding:0;box-sizing:border-box;list-style:none}html,button{font-family:'Roboto', sans-serif}.App{min-height:100vh;background-color:#FFFFFF;background-color:var(--c-bg);text-align:center;color:#572EE5;color:var(--c-purple-secondary);transition:all 0.8s ease}button{cursor:pointer}input,button{outline:none;background:none}.gifs-section{max-width:900px;margin:auto}h2{padding:10px 0}.grid-gifs{display:grid;grid-template-columns:repeat(3, 1fr);grid-gap:30px 47px;padding:28px;background-color:#E7E7E7;background-color:var(--c-bg-gifts)}.grid-gifs figure{justify-self:center;height:250px;width:250px}.grid-gifs figure img{height:100%;width:100%}@media (max-width: 1024px){.gifs-section{max-width:700px;margin:auto}.grid-gifs{display:grid;grid-template-columns:repeat(2, 1fr);grid-gap:30px 0;padding:28px}}@media (max-width: 768px){.gifs-section{max-width:400px;margin:auto}.grid-gifs{display:grid;grid-template-columns:1fr;grid-gap:30px 0;padding:28px}}

.loader-container{display:flex;justify-content:center;align-items:center;padding:50px 0;background-color:var(--c-bg-gifts)}.loader-img{width:100px}

